<template>
  <div :class="$root.themeHomeChange === '1' ? 'companyCentersLight' : 'companyCenters'">
    <div class="left">
      <el-menu :default-active="$route.query.index"
               class="el-menu-vertical"
               :text-color="$root.themeHomeChange === '1' ? '#fff' : '#fff'"
               @select="handleSelect"
               :collapse="isCollapse"
               >
        <div v-for="item in menuList" :key="item.index">
          <el-menu-item v-if="item.children.length === 0" :index="item.index">
            <i :class="item.icon"></i>
            <span slot="title">{{item.title}}</span>
          </el-menu-item>
          <el-submenu v-else index="2">
            <template slot="title">
              <i :class="item.icon"></i>
              <span slot="title">{{item.title}}</span>
            </template>
            <el-menu-item-group>
              <div v-for="item1 in item.children" :key="item1.index">
                <el-menu-item v-if="item1.children.length === 0" :index="item1.index">
                  <i :class="item1.icon"></i>
                  <span slot="title">{{item1.title}}</span>
                </el-menu-item>
                <el-submenu v-else index="2">
                  <template slot="title">
                    <i :class="item1.icon"></i>
                    <span slot="title">{{item1.title}}</span>
                  </template>
                  <el-menu-item-group>
                    <div v-for="item2 in item1.children" :key="item2.index">
                      <el-menu-item v-if="item2.children.length === 0" :index="item2.index">
                        <i :class="item2.icon"></i>
                        <span slot="title">{{item2.title}}</span>
                      </el-menu-item>

                    </div>
                  </el-menu-item-group>
                </el-submenu>
              </div>
            </el-menu-item-group>
          </el-submenu>
        </div>
      </el-menu>
      <el-button :icon="isCollapse ? 'el-icon-d-arrow-right' : 'el-icon-d-arrow-left'"
                 class="btnGroup"
                 @click="() => { isCollapse = !isCollapse }"
                 type="primary"
      ></el-button>
    </div>
    
    <div class="right" ref="companyCenterMain">
      <router-view :routerList="routerList" @handleSelect="handleSelect"></router-view>
    </div>
  </div>
    
</template>

<script>
  import bus from '@/utils/bus'
  export default {
    name: "riskControlCenter",
    data(){
      return {
        isCollapse : true,
        routerList: [],
        menuList: [
          {
            title: '总览',
            path: '/companyCenterMain',
            index: '2-1',
            icon: 'el-icon-data-line',
            children: [
            ]
          },
          {
            title: '知识库',
            path: '/regulationTable',
            index: '2-2',
            icon: 'el-icon-folder-opened',
            children: [
            ]
          },
          {
            title: '规则库',
            path: '/companyCenterManage',
            index: '2-3',
            icon: 'el-icon-document',
            children: []
          },
          {
            title: '基础数据维护',
            path: '/baseDataView',
            index: '2-4',
            icon: 'el-icon-s-tools',
            children: []
          }
        ]
      }
    },
    mounted() {},
    created() {
      this.setRouterList(this.menuList);
      if(this.$route.path === '/companyCenter') {
        this.init(this.$route.query.index);
      }
    },
    watch: {
      $route(v) {
        if(v.path === '/companyCenter') {
          this.init(this.$route.query.index);
        }
      },
      isCollapse(v){
        bus.$emit('isCollapse',v)
      }
    },
    methods: {
      init(index) {
        index = index ? index : '2-1';
        this.setRouterList(this.menuList);
        this.$router.push({
          path: this.routerList[index],
          query: {
            ...this.$route.query,
            index: index,
            actNav: this.$route.query.actNav
          }
        });
      },
      // 设置回调函数，取出树状数据中的所需数据，扁平化，减少后续循环
      setRouterList(data) {
        data.forEach(item => {
          if(item.children.length === 0) {
            this.routerList[item.index] = item.path;
          } else {
            this.setRouterList(item.children);
          }
        })
      },
      handleSelect(key) {
        // console.log(key, keyPath, this.routerList[key]);
        // 获取设置的路由地址
        this.$router.push({
          path: this.routerList[key],
          query: {
            index: key,
            actNav: this.$route.query.actNav
          }
        });
      }
    }
  }
</script>
<style scoped lang="scss">
  .companyCentersLight {
    height: 100%;
    display: flex;
    justify-content: space-between;
    /deep/ .el-menu {
      background: #fff;
      border: none;
      .el-menu-item:not(.is-active) {
        i {
          color: #4389f9;
        }
        &:hover {
          color: #4389f9;
          background: rgba($color: #4389f9, $alpha: .3);
        }
      }
      .el-menu-item span{
        color: #8c8c8c;
      }
      .el-menu-item.is-active span{
        color: #fff!important;
      }
      .el-menu-item.is-active{
        background: #4389f9;
      }
    }
    .left {
      width: auto;
      height: 100%;
      background: #fff;
      position: relative;
      .el-menu-vertical:not(.el-menu--collapse) {
        min-width: 150px;
        font-size: 14px;
        font-weight: bold;
      }
      .btnGroup {
        position: absolute;
        z-index: 999;
        width: 25px;
        border-radius: 0 5px 5px 0;
        padding: 14px 4px;
        right: -25px;
        top: 50%;
      }
    }

    .right{
      width: 100%;
      overflow-y: auto;
      background: #eef3ff;
    }
  }
</style>